.User-dropdown {
  position: relative;
}
.User-dropdown ul {
  position: absolute;
  top: 25px;
  right: -50px;
  border: 1px solid #dfe1e5;
  box-shadow: 1px #dfe1e5;
  width: 175px;
  list-style: none;
  overflow: hidden;
  border-radius: 5px;

  transition: ease 0.4s;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);

  padding: 10px;
}
.User-dropdown a {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 0.5rem;
