}
.menu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s ease, visibility 0.4s, -webkit-transform 0.4s ease;
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s, -webkit-transform 0.4s ease;
}
.menu.active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.4s ease, visibility 0.4s, -webkit-transform 0.4s ease;
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s, -webkit-transform 0.4s ease;
}

.productContent {
  display: flex;
  justify-content: center;
  align-content: center;
  min-height: 100vh;
  margin: 0px auto;
  width: 1200px;

  flex-flow: row wrap;
}

.productCard {
  background: #ffffff;

  border-radius: 15px;
  border: 1px solid #ccc;
  position: relative;
  display: inline-block;
  width: 170px;
  margin: 10px 10px 0 10px;
  height: 310px;
}

.productContent .productCard .productImage {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.productContent .productCard .productImage img {
  object-fit: cover;
  transition: 0.5s ease-in-out;
  -webkit-transform-origin: right;
          transform-origin: right;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.productContent .productCard:hover .productImage {
  cursor: pointer;
}

.productContent .productName {
  font-size: 15px;

  font-family: "Poppins", sans-serif;
}
.productContent .productName h3 {
  margin: 0 0 0 5px;
  font-size: 15px;
  line-height: 1.5;

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 600;
  color: #333;
}
.price {
  margin: 0 0 0 5px;
  font-family: "Work Sans", sans-serif;
  font-weight: 999;
  font-size: 16px;
  color: rgb(81, 91, 93);
  text-decoration: none;
  display: flex;
  align-items: baseline;
}
.productContent .price .priceBeforDiscount {
  margin: 0 0 0 5px;

  font-weight: 600;

  text-decoration: none;
  display: flex;

  font-family: SourceSansPro, Arial, sans-serif;
  font-weight: 600;
  letter-spacing: normal;
  text-align: right;
}
.productContent .price .Discount:after {
  content: "";
  position: absolute;
  left: 0px;
  color: rgb(73, 71, 69);
  width: 100%;
  pointer-events: none;
  top: calc(50% - 2px);
  height: 2px;
  border-top: 2px solid;
}

.productContent .price .priceAfterDiscount {
  margin: 0 0 0 5px;

  color: rgb(223, 0, 0);
