.custom-file {
  position: relative;
  display: inline-block;
  width: 90.5%;
  height: calc(1.5em + 0.75rem + 2px);

  margin-bottom: 35px !important;
  box-sizing: border-box;
}
.custom-file-error {
  position: relative;
  display: inline-block;
  width: 90.5%;
  height: calc(1.5em + 0.75rem + 2px);

  margin-bottom: 25px !important;
  box-sizing: border-box;
}
.custom-file-input {
  position: relative;
  z-index: 2;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  margin: 0;
  opacity: 0;
  box-sizing: border-box;
}
.custom-file-label {
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  padding: 16.5px 14px;
  box-sizing: border-box;
  padding: 16.5px 14px;
  box-sizing: content-box;
  position: absolute;
  height: 1.4375em;
}

.custom-file-label::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  display: block;
  height: calc(1.5em + 0.75rem);
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  color: #495057;
